<template>
  <form @submit.prevent="submitForm" class="client-add">

    <div class="client-add__inner">

      <h1 class="client-add__inner--title">Новый клиент</h1>

      <p class="client-add__regulation">Поля отмеченные <span class="client-add__regulation--icon"><Error/></span>, обязательны для заполнения</p>

      <!-- Client info -->

      <Info></Info>

      <!-- Client addrees -->

      <Addrees></Addrees>

      <!-- Client docs -->

      <Docs></Docs>

      <div class="client-add__prop--result">
        <button class="client-add__button">Добавить клиента</button>
        <span class="client-add__notify--result"></span>
      </div> 

    </div>
  </form>
</template>
<script>
import Info from "@/components/Form/blocks/Info.vue"
import Addrees from "@/components/Form/blocks/Addrees.vue"
import Docs from "@/components/Form/blocks/Docs.vue"
import Error from "@/components/Icons/Error.vue"
import eventBus from "@/eventBus.js"

export default {
  name: 'Form',
  components: {
    Info,
    Addrees,
    Docs,
    // Tick,
    Error,
  },
  data() {
    return {
      errors: [],
    }
  },
  methods: {
   submitForm() {

      let result = document.querySelector('.client-add__notify--result');

      console.log(this.birth, this.dateDoc);

      if (!this.birth && !this.dateDoc) {
       
        result.classList.remove('done');
        result.classList.add('fail');
        result.innerHTML="Клиент не добавлен";

      } else {
           let arrBirth = this.birth.split('.'),
               arrDoc = this.dateDoc.split('.');
            
            if(!this.lastname) {
      this.errors.push('Введите фамилию')
    }
    if(!this.name) {
      this.errors.push('Введите имя')
    }
    if(arrBirth[0].length != 2 && arrBirth[1].length != 2 && arrBirth[2].length != 4 && arrBirth.lenght != 10) {
      this.errors.push('Укажите дату рождения в формате дд.мм.гггг')
    }
    if(this.group.length == 0) {
      this.errors.push('Укажите гуппу')
    }
    if(!this.city) {
      this.errors.push('Укажите город')
    }
    if(!this.typeDoc) {
      this.errors.push('Укажите тип документа')
    }
    if (arrDoc[0].length != 2 && arrDoc[1].length != 2 && arrDoc[2].length != 4&& arrDoc.length != 10 && this.dateDoc == undefined) {
      this.errors.push('Укажите дату выдачи документа в формате дд.мм.гггг')
    }
      
        if(this.lastname && this.name && arrBirth[0].length == 2 && arrBirth[1].length == 2 && arrBirth[2].length == 4 && this.group.length != 0 && this.city && this.typeDoc && arrDoc[0].length == 2 && arrDoc[1].length == 2 && arrDoc[2].length == 4) {
        result.classList.remove('fail');
        result.classList.add('done');
        result.innerHTML="Клиент успешно добавлен";
    } else {
        result.classList.remove('done');
        result.classList.add('fail');
        result.innerHTML="Клиент не добавлен";
     }
   }

    }
  },
    created() {
      eventBus.$on('Info', ({lastname, name, birth, group}) => {
        this.lastname = lastname;
        this.name = name;
        this.birth = birth;
        this.group = group;
    })
      eventBus.$on('Addrees', ({city}) => {
        this.city = city;
      })
      eventBus.$on('Addrees', ({city}) => {
        this.city = city;
      })
      eventBus.$on('Docs', ({typeDoc, dateDoc}) => {
        this.typeDoc = typeDoc;
        this.dateDoc = dateDoc;
      })
    }
  }
</script>

