<template>
<div class="client-add__addrees">
      
        <h2 class="client-add__title">Адрес</h2>

        <div class="client-add__prop">
          <b>Индекс:</b>
          <input class="client-add__prop--input" type="text" placeholder="123456">
        </div>

        <div class="client-add__prop">
          <b>Страна:</b>
          <input class="client-add__prop--input" type="text" placeholder="Россия">
        </div>

        <div class="client-add__prop">
          <b>Область:</b>
          <input class="client-add__prop--input" type="text" placeholder="123456">
        </div>

        <div class="client-add__prop">
          <b>Город:</b>
          <input  class="client-add__prop--input" type="text" placeholder="Тверь" v-model.lazy="city">
          <span class="client-add__notify" v-if="!this.city"><Error/></span><span v-else class="client-add__notify"><Tick/></span>
        </div>

        <div class="client-add__prop">
          <b>Улица:</b>
          <input class="client-add__prop--input" type="text" placeholder="ул. Ленина">
        </div>

        <div class="client-add__prop">
          <b>Дом:</b>
          <input class="client-add__prop--input" type="text" placeholder="14">
        </div>

      </div>
</template>
<script>
import eventBus from '@/eventBus.js'
import Tick from "@/components/Icons/Tick.vue"
import Error from "@/components/Icons/Error.vue"

export default {
    name: "Addrees",
    components: {
        Tick,
        Error
    },
    data() {
      return {
          city: "",
      }
    },
    updated() {
      eventBus.$emit('Addrees', {
        city: this.city
      })
    }
