<template>
  <div class="client-add__info">
      
    <h2 class="client-add__title">Информация о клиенте</h2>

      <div class="client-add__block">

        <div class="client-add__prop">
          <b>Фамилия:</b>
          <input class="client-add__prop--input" type="text" onkeyup="this.value = this.value.replace(/[^a-zA-Zа-яА-Я-]/, '')" placeholder="Иванов" v-model.lazy="lastname">
          <span class="client-add__notify" v-if="!this.lastname"><Error/></span><span class="client-add__notify" v-else><Tick/></span>
        </div>

        <div class="client-add__prop">
          <b>Имя:</b>
          <input class="client-add__prop--input" type="text" onkeyup="this.value = this.value.replace(/[^a-zA-Zа-яА-Я-]/, '')" placeholder="Иван" v-model.lazy="name">
          <span class="client-add__notify" v-if="!this.name"><Error/></span><span class="client-add__notify" v-else><Tick/></span>
        </div>

        <div class="client-add__prop">
          <b>Отчество:</b>
          <input class="client-add__prop--input" type="text" onkeyup="this.value = this.value.replace(/[^a-zа-я-]/, '')" placeholder="Иванович">
        </div>

      </div>

      <div class="client-add__block">
        
        <div class="client-add__prop">
            <b>Пол:</b>
          <div class="client-add__gender">
            <span class="client-add__gender--row">
              <input class="client-add__checkbox" name="gender" type="radio" id="male">
              <span class="client-add__radio-fake"></span>
              <label class="client-add__label" for="male">Мужской</label>
            </span>
            <span class="client-add__gender--row">
              <input class="client-add__checkbox" name="gender" type="radio" id="female">
              <span class="client-add__radio-fake"></span>
              <label class="client-add__label" for="female">Женский</label>
            </span>
          </div>
        </div>

        <div class="client-add__prop" :class="{'form-group--error': $v.birth.$error}">
          <b>Дата рождения:</b>
          <input class="client-add__prop--input-date" type="text" onkeyup="this.value = this.value.replace (/[^0-9.]/, '')" placeholder="ДД.ММ.ГГГГ" v-model.lazy="$v.birth.$model">
          <span class="client-add__notify" v-if="!$v.birth.required"><Error/></span>
          <span class="client-add__notify--error" v-else-if="!$v.birth.minLength"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify--error" v-else-if="!$v.birth.maxLength"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify--error" v-else-if="!$v.birth.validDate"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify" v-else><Tick/></span>
        </div>

      </div>

      <div class="client-add__block">

        <div class="client-add__prop--group">
          <b>Группа клиентов:</b>
          <v-select class="client-add__prop--input-multiple" multiple :options="['VIP', 'Проблемные', 'ОМС']"></v-select>
          <span class="client-add__notify" v-if="this.group.length == 0"><Error/></span><span v-else class="client-add__notify"><Tick/></span>
        </div>

        <div class="client-add__prop">
          <b>Номер телефона:</b>
          <input class="client-add__prop--input" type="text" onkeyup="this.value = this.value.replace(/[^0-9-+]/, '')" placeholder="+7-123-456-7890">
        </div>
        
        <div class="client-add__prop-sms">
          <input class="client-add__checkbox" type="checkbox" id="sms">
          <span class="client-add__checkbox-fake"></span>
          <label class="client-add__label" for="sms">Не отправлять СМС</label>
        </div>
      
      <div class="client-add__prop">
        <b>Лечащий врач:</b>
        <select class="client-add__prop--input-select">
          <option>Иванов</option>
          <option>Захаров</option>
          <option>Чернышева</option>
        </select> 
      </div>

    </div>  
  </div>
</template>
<script>
import {required, minLength, maxLength, helpers} from 'vuelidate/lib/validators'
import Tick from "@/components/Icons/Tick.vue"
import Error from "@/components/Icons/Error.vue"
import eventBus from "@/eventBus.js"
import vSelect from 'vue-select'

export default {
  name: "Info",
  components: {
    Tick,
    Error,
    vSelect
  },
  data() {
    return {
      lastname: "",
      name: "",
      birth: "",
      group: "",
    }
  },
  validations: {
    birth: {
      required,
      minLength: minLength(10),
      maxLength: maxLength(10),
      validDate: helpers.regex('alpha', /(0[1-9]|[12][0-9]|3[01])[.](0[1-9]|1[012])[.](19|20)\d\d/)
    }
  },
  updated() {
    eventBus.$emit('Info', {
      lastname: this.lastname,
      name: this.name,
      birth: this.birth,
      group: this.group
    })
  },
}
</script>