<template>
       <div class="client-add__docs">
      
        <h2 class="client-add__title">Документы</h2>

        <div class="client-add__prop">
          <b>Тип документа:</b>
          <select class="client-add__prop--input-select" v-model.lazy="typeDoc">
            <option>Паспорт</option>
            <option>Свидетельство о рождении</option>
            <option>Водительское удостоверение</option>
          </select>
          <span class="client-add__notify" v-if="!this.typeDoc"><Error/></span><span v-else class="client-add__notify"><Tick/></span>
        </div>

        <div class="client-add__prop">
          <b>Серия:</b>
          <input class="client-add__prop--input" type="text" placeholder="12 34">
        </div>

        <div class="client-add__prop">
          <b>Номер:</b>
          <input class="client-add__prop--input" type="text" placeholder="567890">
        </div>

        <div class="client-add__prop">
          <b>Кем выдан:</b>
          <input class="client-add__prop--input" type="text" placeholder="Ленинским ОВД г. Твери">
        </div>

        <div class="client-add__prop">
          <b>Дата выдачи:</b>
          <input class="client-add__prop--input-date" type="text" onkeyup="this.value = this.value.replace (/[^0-9.]/, '')" placeholder="ДД.ММ.ГГГГ" v-model="$v.dateDoc.$model">
          <span class="client-add__notify" v-if="!$v.dateDoc.required"><Error/></span>
          <span class="client-add__notify--error" v-else-if="!$v.dateDoc.minLength"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify--error" v-else-if="!$v.dateDoc.maxLength"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify--error" v-else-if="!$v.dateDoc.validDate"><span class="client-add__notify"><Error/></span> Формат даты должен быть ДД.ММ.ГГГГ </span>
          <span class="client-add__notify" v-else><Tick/></span>
        </div>
       
      </div>
</template>
<script>
  import eventBus from '@/eventBus.js'
  import { required, minLength, maxLength, helpers} from 'vuelidate/lib/validators'
  import Tick from "@/components/Icons/Tick.vue"
  import Error from "@/components/Icons/Error.vue"

  export default {
    name: "Docs",
    components: {
        Tick,
        Error
    },
    data() {
      return {
        typeDoc: "",
        dateDoc: "",
      }
    },

    validations: {
      dateDoc: {
        required,
        minLength: minLength(10),
        maxLength: maxLength(10),
        validDate: helpers.regex('alpha', /(0[1-9]|[12][0-9]|3[01])[.](0[1-9]|1[012])[.](19|20)\d\d/)
      }
    },

    updated() {
      eventBus.$emit('Docs', {
        typeDoc: this.typeDoc,
        dateDoc: this.dateDoc,
      })
    }
  }
</script>